import React, { Component } from "react";
import styled from "styled-components";
import MOCK_DATA from "../../MOCK_DATE.json";
import { Modal, List, Button, DatePicker, TextareaItem } from "antd-mobile";

import enUs from "antd-mobile/lib/date-picker/locale/en_US";

import Menu from "../menu/Menu";
import HorizontalCalendar from "./HorizontalCalendar";
import VerticalCalendar from "./VerticalCalendar";

const calendarData = MOCK_DATA;

let headerVisible = true;

const HeaderWrapper = styled.div`
  position: fixed;
  width: 100%;
  display: ${headerVisible ? "display" : "none"};
`;

const ContnetWrapper = styled.div`
  padding-top: 125px;
`;

const RenderMonth = styled.div`
  height: 30px;
  text-align: center;
  font-size: 20px;
  font-weight: 600;
  padding: 5px;
  font-family: "맑은 고딕";
  background-color: #ffffff;
`;

const WrapperHorizontalCalendar = styled.div`
  padding: 5px;
  height: 30px;
  background-color: #e5e5e5;
  width: 100%;
  overflow: auto;
  ::-webkit-scrollbar {
    display: none;
  }
`;

const WrapperVerticalCalendar = styled.div`
  /* padding: 20px; */
  background-color: #ffffff;
`;

const PlusButton = styled.div`
  height: 50px;
  width: 50px;
  border-radius: 25px;
  background-color: #ff0000;
  color: #ffffff;
  font-weight: 900;
  font-size: 35px;
  text-align: center;
  position: fixed;
  bottom: 10px;
  right: 10px;
`;

export default class Content extends Component {
  state = {
    modal1: false,
    modal2: false
  };
  showModal = key => e => {
    e.preventDefault();
    this.setState({
      [key]: true
    });
  };
  onClose = key => () => {
    this.setState({
      [key]: false
    });
  };

  render() {
    return (
      <div>
        <HeaderWrapper>
          <Menu />
          <RenderMonth>6 월</RenderMonth>
          <WrapperHorizontalCalendar>
            <HorizontalCalendar calendarData={calendarData} />
          </WrapperHorizontalCalendar>
        </HeaderWrapper>
        <ContnetWrapper>
          <WrapperVerticalCalendar>
            <VerticalCalendar calendarData={calendarData} />
          </WrapperVerticalCalendar>
          <PlusButton onClick={this.showModal("modal2")}>+</PlusButton>
        </ContnetWrapper>
        <Modal
          popup
          visible={this.state.modal2}
          onClose={this.onClose("modal2")}
          animationType="slide-up"
          afterClose={() => {
            // alert("afterClose");
          }}
        >
          <List
            renderHeader={() => <div>근로시간 등록</div>}
            className="popup-list"
          >
            <DatePicker
              mode="date"
              locale={enUs}
              title="Select Date"
              extra="Optional"
              value={this.state.date}
              onChange={date => this.setState({ date })}
            >
              <List.Item arrow="horizontal">Date</List.Item>
            </DatePicker>
            <DatePicker
              mode="time"
              locale={enUs}
              minuteStep={2}
              use12Hours
              value={this.state.time}
              onChange={time => this.setState({ time })}
            >
              <List.Item arrow="horizontal">Time (am/pm)</List.Item>
            </DatePicker>
            <TextareaItem
              title="사유"
              placeholder="click the button below to focus"
              data-seed="logId"
              autoHeight
              rows={3}
              ref={el => (this.customFocusInst = el)}
            />
            <List.Item>
              <Button type="primary" onClick={this.onClose("modal2")}>
                닫기
              </Button>
            </List.Item>
          </List>
        </Modal>
      </div>
    );
  }
}

